.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* App-wide styles */
body {
  background: #f4f6fb;
  font-family: 'Segoe UI', Arial, sans-serif;
}

form {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  padding: 2rem 2.5rem 1.5rem 2.5rem;
  margin: 2rem auto;
  max-width: 400px;
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

form h2 {
  color: #2d3a4b;
  margin-bottom: 0.5rem;
}

input, select {
  padding: 0.7rem 1rem;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  font-size: 1rem;
  background: #f9fafb;
  transition: border 0.2s;
}

input:focus, select:focus {
  border: 1.5px solid #4f8cff;
  outline: none;
}

button[type="submit"] {
  background: linear-gradient(90deg, #4f8cff 0%, #38b6ff 100%);
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 0.8rem 0;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  margin-top: 0.5rem;
  transition: background 0.2s;
}

button[type="submit"]:hover {
  background: linear-gradient(90deg, #38b6ff 0%, #4f8cff 100%);
}

/* Navigation menu styles */
.nav-menu {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 2rem;
}

.nav-menu button {
  background: #f4f6fb;
  border: 1.5px solid #4f8cff;
  color: #2d3a4b;
  padding: 0.7rem 1.5rem;
  border-radius: 6px 6px 0 0;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
  outline: none;
}

.nav-menu button.active, .nav-menu button:hover {
  background: #4f8cff;
  color: #fff;
  border-bottom: 2.5px solid #fff;
}

/* Table styles */
.expense-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 1.5rem;
  background: #fff;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0,0,0,0.06);
}
.expense-table th, .expense-table td {
  border: 1px solid #e0e6ed;
  padding: 10px 14px;
  text-align: left;
  font-size: 1rem;
}
.expense-table th {
  background: #f4f6fb;
  color: #2d3a4b;
  font-weight: 600;
}
.expense-table tr:nth-child(even) {
  background: #f9fafb;
}
.expense-table tr:hover {
  background: #eaf3ff;
}
.expense-table td {
  color: #2d3a4b;
}

@media (max-width: 700px) {
  .expense-table th, .expense-table td {
    font-size: 0.95rem;
    padding: 7px 6px;
  }
}

/* Responsive */
@media (max-width: 500px) {
  form {
    padding: 1rem;
    max-width: 95vw;
  }
}

.App h1 {
  color: #2d3a4b;
  margin-top: 2rem;
  margin-bottom: 1.5rem;
  font-size: 2.2rem;
  letter-spacing: 1px;
}

/* Category form styles */
.category-form {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  padding: 2rem 2.5rem 1.5rem 2.5rem;
  max-width: 400px;
  margin: 2rem auto;
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
.category-form-title {
  color: #2d3a4b;
  margin-bottom: 0.5rem;
}
.category-form-input {
  padding: 0.7rem 1rem;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  font-size: 1rem;
  background: #f9fafb;
  transition: border 0.2s;
}
.category-form-input:focus {
  border: 1.5px solid #4f8cff;
  outline: none;
}
.category-form-btn {
  background: linear-gradient(90deg, #4f8cff 0%, #38b6ff 100%);
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 0.8rem 0;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  margin-top: 0.5rem;
  transition: background 0.2s;
}
.category-form-btn:hover {
  background: linear-gradient(90deg, #38b6ff 0%, #4f8cff 100%);
}
.category-list-title {
  color: #2d3a4b;
  margin-top: 2rem;
  margin-bottom: 1rem;
}
.category-list {
  list-style: none;
  padding: 0;
  max-width: 400px;
  margin: 0 auto;
}
.category-list-item {
  background: #f9fafb;
  border-radius: 6px;
  margin-bottom: 8px;
  padding: 10px 16px;
  color: #2d3a4b;
  font-size: 1rem;
  box-shadow: 0 1px 4px rgba(0,0,0,0.04);
}
