/* User table styling */
.user-table {
  width: auto;
  min-width: 400px;
  max-width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
}
.user-table th, .user-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}
.user-table th {
  background-color: #f2f2f2;
}

/* Edit button styling */
.user-edit-btn {
  background-color: #4CAF50;
  color: white;
  border: none;
  padding: 6px 12px;
  margin-right: 8px;
  cursor: pointer;
  border-radius: 4px;
}
.user-edit-btn:hover {
  background-color: #45a049;
}

/* Delete button styling */
.user-delete-btn {
  background-color: #f44336;
  color: white;
  border: none;
  padding: 6px 12px;
  cursor: pointer;
  border-radius: 4px;
}
.user-delete-btn:hover {
  background-color: #d32f2f;
}
